* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin-top: 40px;
}

li {
  margin-top: 5px;
}

a, ul {
  text-decoration: none;
  list-style: none;
}

a:hover, a:focus {
  color: #b27fff;
  transition: all .7s;
}

.nav {
  margin-top: 40px;
  margin-bottom: 20px;
  margin-left: 40px;
}

.heading {
  font: inherit;
  margin-bottom: 40px;
  margin-left: 40px;
  font-size: 22px;
}

